// If you want to override variables do it here
@import "variables";

$enable-ltr: true; /* stylelint-disable-line scss/dollar-variable-default */
$enable-rtl: true; /* stylelint-disable-line scss/dollar-variable-default */

// Import styles
@import "coreui/scss/coreui";

@import "layout";

// Prism.js
@import "examples";

// If you want to add custom CSS you can put it here.
@import "custom";

@import "simplebar";

 * {
    font-family: 'Almarai', sans-serif;

}
.ltr {
    direction: ltr;
}
.rtl {
    direction: rtl;
}

@keyframes spin_first {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
.spinner {
    position: absolute;
    animation: spin_first 1.5s infinite;
    z-index: 1;
    top: 45%;
    left: 50%;
}
